@import url('https://fonts.googleapis.com/css2?family=Diphylleia:wght@400&display=swap');

h1, body, html, ::placeholder, input, textarea {
    font-family: 'Diphylleia', sans-serif;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    margin: 0;
    padding: 0;
}

:root {
    --vh: 100vh;
}

.intro-section {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: var(--vh);
  padding: 1.5rem;
  overflow: hidden;
}

.intro-section::before, .intro-section::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: var(--vh);
  width: 100%;
  background-size: 30%;
  background-repeat: repeat-x;
  background-position: bottom;
  transition: opacity 1.5s ease;
  z-index: -1;
}

.intro-section::before {
  background-image: url('../../assets/poppies.png');
  opacity: 1;
}

.intro-section::after {
  background-image: url('../../assets/sunset.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0;
}


  body[data-theme="dark"] .intro-section::before {
      opacity: 0;
  }

  body[data-theme="dark"] .intro-section::after {
      opacity: 1;
  }

.intro-title {
        flex: 1;
        font-size: 2.5rem;
        text-align: center;
}

.intro-title img {
        flex: 1;
        font-size: 2.5rem;
        text-align: center;
        position: absolute;
        left: 0;
        bottom: 0;
}

.intro-image {
        flex: 1;
}


@media screen and (max-width: 820px) {
        .intro-section::before {
          background-size: 50%;
        }
      }

@media screen and (max-width: 500px) {
        .intro-section::before {
          background-size: 100%;
        }
      }